.footer {
	padding: 1rem 0;
	width: 100%;
}

.content {
	display: flex;
	justify-content: center;
	align-items: center;

	h3 {
		color: var(--text-color);
		font-size: 1.5rem;
		opacity: 0.7;
	}
}

@media (max-width: 370px) {

	.content {


		h3 {
			font-size: 1.3rem;
		}
	}
}